import React from "react";
import { Route, Switch, Redirect } from "react-router-dom";
import { TransitionGroup, CSSTransition } from "react-transition-group-v2";
import Header from "../components/header/header";
import Sidebar from "../components/sidebar/sidebar";
import ThemeRoutes from "../routes/routing";

class Fulllayout extends React.Component {
  /*--------------------------------------------------------------------------------*/
  /* Change the layout settings [HEADER,SIDEBAR && DARK LAYOUT] from here            */
  /*--------------------------------------------------------------------------------*/
  constructor(props) {
    super(props);
    const { history } = this.props;
    this.updateDimensions = this.updateDimensions.bind(this);
    this.state = {
      width: window.innerWidth,
      settings: [
        {
          theme: "light",
          layout: "vertical",
          dir: "ltr",
          sidebartype: "full",
          sidebarpos: "fixed",
          headerpos: "fixed",
          boxed: "full",
          navbarbg: "skin4",
          sidebarbg: "skin6",
          logobg: "skin4"
        }
      ],
      user: {
        name: "",
        nCard: "",
        valCard: ""
      },
      cardStatus: 0
    };

    history.listen(() => {
      if (
        window.innerWidth < 767 &&
        document
          .getElementById("main-wrapper")
          .className.indexOf("show-sidebar") !== -1
      ) {
        document
          .getElementById("main-wrapper")
          .classList.toggle("show-sidebar");
      }
    });
  }

  /*--------------------------------------------------------------------------------*/
  /* Life Cycle Hook, Applies when loading or resizing App                           */
  /*--------------------------------------------------------------------------------*/
  componentDidMount() {
    window.addEventListener("load", this.updateDimensions);
    window.addEventListener("resize", this.updateDimensions);
  }

  /*--------------------------------------------------------------------------------*/
  /* Life Cycle Hook                                                                 */
  /*--------------------------------------------------------------------------------*/
  componentWillUnmount() {
    window.removeEventListener("load", this.updateDimensions);
    window.removeEventListener("resize", this.updateDimensions);
  }

  handleComponentChanges = input => e => {
    const { user } = this.state;
    user[input] = e.target.value;
    this.setState({ user });
  };

  changeCardStatus = newStatus => {
    this.setState({ cardStatus: newStatus });
  };

  /*--------------------------------------------------------------------------------*/
  /* Function that handles sidebar, changes when resizing App                        */
  /*--------------------------------------------------------------------------------*/
  updateDimensions() {
    const { settings } = this.state;
    const element = document.getElementById("main-wrapper");
    const width = window.innerWidth;
    this.setState({ width });
    switch (settings[0].sidebartype) {
      case "full":
      case "iconbar":
        if (width < 767) {
          element.setAttribute("data-sidebartype", "mini-sidebar");
          element.classList.add("mini-sidebar");
        } else {
          element.setAttribute("data-sidebartype", "full");
          element.classList.remove("mini-sidebar");
        }
        break;

      case "overlay":
        if (width < 767) {
          console.log("c", width);
          element.setAttribute("data-sidebartype", "mini-sidebar");
        } else {
          console.log("d", width);
          // element.setAttribute("data-sidebartype", settings[0].sidebartype);
        }
        break;

      default:
    }
  }

  render() {
    /*--------------------------------------------------------------------------------*/
    /* Theme Setting && Layout Options wiil be Change From Here                       */
    /*--------------------------------------------------------------------------------*/
    const { settings, cardStatus, user } = this.state;
    const {
      location: { pathname },
      location
    } = this.props;
    return (
      <div
        id="main-wrapper"
        dir={settings[0].dir}
        data-theme={settings[0].theme}
        data-layout={settings[0].layout}
        data-sidebartype={settings[0].sidebartype}
        data-sidebar-position={settings[0].sidebarpos}
        data-header-position={settings[0].headerpos}
        data-boxed-layout={settings[0].boxed}
      >
        {/*--------------------------------------------------------------------------------*/}
        {/* Header                                                                         */}
        {/*--------------------------------------------------------------------------------*/}
        <Header data={this.state} history={this.props.history} />
        {/*--------------------------------------------------------------------------------*/}
        {/* Sidebar                                                                        */}
        {/*--------------------------------------------------------------------------------*/}
        <Sidebar data={this.state} {...this.props} routes={ThemeRoutes} />
        {/*--------------------------------------------------------------------------------*/}
        {/* Page Main-Content                                                              */}
        {/*--------------------------------------------------------------------------------*/}
        <div className="page-wrapper d-block">
          <div className="page-content container-fluid">
            <TransitionGroup>
              <CSSTransition timeout={1000} key={pathname} classNames="fade">
                <Switch location={location}>
                  {ThemeRoutes.map(prop => {
                    if (prop.navlabel) {
                      return null;
                    }
                    if (prop.collapse) {
                      return prop.child.map(prop2 => {
                        if (prop2.collapse) {
                          return prop2.subchild.map(prop3 => (
                            <Route
                              path={prop3.path}
                              render={props => (
                                <prop3.component
                                  {...props}
                                  user={user}
                                  changeCardStatus={this.changeCardStatus}
                                  cardStatus={cardStatus}
                                  handleComponentChanges={
                                    this.handleComponentChanges
                                  }
                                />
                              )}
                              // key={prop3.component}
                              key={pathname}
                            />
                          ));
                        }
                        return (
                          <Route
                            path={prop2.path}
                            render={props => (
                              <prop2.component
                                {...props}
                                user={user}
                                changeCardStatus={this.changeCardStatus}
                                handleComponentChanges={
                                  this.handleComponentChanges
                                }
                                cardStatus={cardStatus}
                              />
                            )}
                            key={pathname}
                            changeCardStatus={this.changeCardStatus}
                          />
                        );
                      });
                    }
                    if (prop.redirect) {
                      return (
                        <Redirect
                          from={prop.path}
                          to={prop.pathTo}
                          key={pathname}
                        />
                      );
                    }
                    return (
                      <Route
                        path={prop.path}
                        render={props => (
                          <prop.component
                            {...props}
                            user={user}
                            handleComponentChanges={this.handleComponentChanges}
                            changeCardStatus={this.changeCardStatus}
                            cardStatus={cardStatus}
                          />
                        )}
                        key={pathname}
                      />
                    );
                  })}
                </Switch>
              </CSSTransition>
            </TransitionGroup>
          </div>
        </div>
      </div>
    );
  }
}
export default Fulllayout;



