import React, { Component } from "react";
import {
  Modal,
  ModalBody,
  Button,
  Row,
  Col,
  Form,
  Container
} from "reactstrap";
import Axios from "axios";
import InputBanky from "../../components/input-banky/input-banky";
import Validator from "./validator";

const Spinner = require("react-spinkit");

export class queueForm extends Component {
  state = {
    cnpj: "",
    email: "",
    celular: "",
    url: "",
    nome: "",
    modalError: false,
    modalLoading: false
  };

  constructor(props) {
    super(props);
    const { nextStep } = this.props;
    this.validators = Validator;
    this.nextStep = nextStep;
  }

  handleChange = e => {
    const change = {};
    change[e.target.name] = e.target.value;
    this.setState(change);
  };

  handleClick = e => {
    const { cnpj, email, celular, url, nome } = this.state;
    e.preventDefault();
    const res = this.validForm();

    if (res) {
      this.toggleLoading();
      Axios.post(
        "https://55v22mz138.execute-api.us-east-1.amazonaws.com/prod/banky/v1/waitlist",
        {
          cnpj,
          email,
          celular,
          url,
          nome
        }
      )
        .then(() => {
          // this.toggleLoading();
          this.nextStep();
        })
        .catch(error => {
          this.toggleLoading();
          this.toggleError();
          console.log(error);
        });
    }
  };

  toggleError = () => {
    const { modalError } = this.state;
    this.setState({ modalError: !modalError });
  };

  toggleLoading = () => {
    const { modalLoading } = this.state;
    this.setState({ modalLoading: !modalLoading });
  };

  validForm() {
    let status = true;
    Object.keys(this.validators).forEach(field => {
      const { [field]: currentState } = this.state;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }

  showErrors(fieldName) {
    if (fieldName === "") return <div className="error" />;
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
          <br />
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(prevState => prevState);
  }

  render() {
    const {
      cnpj,
      email,
      celular,
      url,
      nome,
      modalError,
      modalLoading
    } = this.state;
    const space = "\u2007";
    return (
      <Container fluid className="QueueForm">
        <div className={`loadingBackBlack ${modalLoading ? "active" : ""}`}>
          <div className="spinner-wrapper">
            <div className="text-white mb-3">Finalizando</div>
            <Spinner
              name="folding-cube"
              color="#FFF"
              className="spinnerCenter"
            />
          </div>
        </div>
        {/*              */}
        {/* Introduction */}
        {/*              */}
        <Row noGutters className="my-3 justify-content-center">
          <Col className="text-center introduction">
            Vamos descomplicar? <br />
            Pea j o carto corporativo Banky <br />
            Entre agora mesmo em nossa lista de espera.
          </Col>
        </Row>
        {/*      */}
        {/* Form */}
        {/*      */}
        <Form className="form-material">
          <Row className="justify-content-center align-content-top">
            <Col xs="12" className="mx-auto">
              <InputBanky
                label="CNPJ"
                type="text"
                onChange={this.handleChange}
                mask={this.validators.cnpj.mask}
                value={cnpj}
                name="cnpj"
                formNoValidate
                required
              />
              {this.showErrors("cnpj")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="E-mail"
                mask={this.validators.email.mask}
                onChange={this.handleChange}
                type="email"
                value={email}
                name="email"
                formNoValidate
                required
              />
              {this.showErrors("email")}
            </Col>

            <Col xs="12" lg="5" className="mx-auto">
              <InputBanky
                label="Celular"
                type="tel"
                formatChars={{ "9": "[0-9]", t: "[0-9 ]", "?": "[0-9 ]" }}
                maskChar={space}
                mask={this.validators.celular.mask(celular)}
                onChange={this.handleChange}
                value={celular}
                name="celular"
                formNoValidate
                required
              />
              {this.showErrors("celular")}
            </Col>
            <Col xs="12" lg="7" className="mx-auto">
              <InputBanky
                label="Site (Opcional)"
                type="url"
                mask={this.validators.url.mask}
                onChange={this.handleChange}
                value={url}
                formNoValidate
                name="url"
              />
              {this.showErrors("url")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Nome para contato"
                type="text"
                mask={this.validators.nome.mask}
                onChange={this.handleChange}
                value={nome}
                formNoValidate
                name="nome"
              />
              {this.showErrors("nome")}
            </Col>

            <Col xs="12" className="mx-auto">
              <Button
                size="lg"
                block
                type="submit"
                onClick={this.handleClick}
                className="btn btn-light text-primary"
              >
                Entrar na lista de espera
              </Button>
            </Col>
          </Row>
        </Form>
        <Modal isOpen={modalError} toggle={this.toggleError}>
          <ModalBody className="text-danger">
            <i className="fa fa-exclamation-circle mr-2" />
            No foi possvel finalizar seu cadastro, tente novamente.
          </ModalBody>
        </Modal>
      </Container>
    );
  }
}

export default queueForm;



