import Home from "../views/home/home";
import Statement from "../views/statement/statement";
import Block from "../views/block/block";
import Change from "../views/change/change";
import { RechargeNow } from "../views/recharge/recharge-now/recharge-now";
import { RechargeAuto } from "../views/recharge/recharge-auto/recharge-auto";
import { CardActivation } from "../views/card-activation/card-activation";
import { TalkToUs } from "../views/talk-to-us/talk-to-us";
import { MyAccount } from "../views/my-account/my-account";

const ThemeRoutes = [
  {
    path: "/home",
    name: "Home",
    icon: "ti-home",
    component: Home
  },
  {
    path: "/statement",
    name: "Extrato",
    icon: "ti-layout-list-thumb",
    component: Statement
  },
  {
    collapse: true,
    path: "/recarregar",
    name: "Carregar Carto",
    icon: "ti-reload",
    child: [
      {
        path: "/recarregar/recorrente",
        name: "Recarga recorrente",
        mini: "B",
        icon: "ti-reload",
        component: RechargeAuto
      },
      {
        path: "/recarregar/boleto",
        name: "Carregar Carto",
        mini: "B",
        icon: "ti-reload",
        component: RechargeNow
      }
    ]
  },
  {
    path: "/bloquear",
    name: "Bloquear Carto",
    icon: "ti-credit-card",
    component: Block
  },
  {
    path: "/alterar-senha",
    name: "Alterar Senha do Carto",
    icon: "ti-key",
    component: Change
  },

  {
    path: "/fale-conosco",
    name: "Fale Conosco",
    icon: "ti-comment",
    component: TalkToUs
  },
  {
    path: "/ativar-cartao",
    notInSidebar: true,
    component: CardActivation
  },
  {
    path: "/minha-conta",
    name: "Minha Conta",
    icon: "ti-user",
    component: MyAccount
  },
  { path: "/", pathTo: "/home", name: "Home", redirect: true }
];
export default ThemeRoutes;



