import React from "react";
import {
  Nav,
  NavItem,
  NavLink,
  Button,
  Navbar,
  NavbarBrand,
  Collapse,
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter
} from "reactstrap";
import {} from "react-router-dom";
/*--------------------------------------------------------------------------------*/
/* Import images which are need for the HEADER                                    */
/*--------------------------------------------------------------------------------*/

import logolighttext from "../../assets/images/logo-text-white.svg";

class Header extends React.Component {
  constructor(props) {
    super(props);
    this.toggle = this.toggle.bind(this);
    this.showMobilemenu = this.showMobilemenu.bind(this);
    this.sidebarHandler = this.sidebarHandler.bind(this);
    this.state = {
      isOpen: false,
      loggoutModal: false
    };
  }

  /*--------------------------------------------------------------------------------*/
  /* To open NAVBAR in MOBILE VIEW                                                   */
  /*--------------------------------------------------------------------------------*/
  toggle = () => {
    const { isOpen } = this.state;
    this.setState({
      isOpen: !isOpen
    });
  };

  loggout = () => {
    const { history } = this.props;
    history.push("/login");
  };

  toggleLoggoutModal = () => {
    const { loggoutModal } = this.state;
    this.setState({
      loggoutModal: !loggoutModal
    });
  };

  /*--------------------------------------------------------------------------------*/
  /* To open SIDEBAR-MENU in MOBILE VIEW                                             */
  /*--------------------------------------------------------------------------------*/
  showMobilemenu = () => {
    document.getElementById("main-wrapper").classList.toggle("show-sidebar");
  };

  sidebarHandler = () => {
    const { data } = this.props;
    const element = document.getElementById("main-wrapper");
    switch (data.settings[0].sidebartype) {
      case "full":
      case "iconbar":
        element.classList.toggle("mini-sidebar");
        if (element.classList.contains("mini-sidebar")) {
          element.setAttribute("data-sidebartype", "mini-sidebar");
        } else {
          element.setAttribute(
            "data-sidebartype",
            data.settings[0].sidebartype
          );
        }
        break;

      case "overlay":
      case "mini-sidebar":
        element.classList.toggle("full");
        if (element.classList.contains("full")) {
          element.setAttribute("data-sidebartype", "full");
        } else {
          element.setAttribute(
            "data-sidebartype",
            data.settings[0].sidebartype
          );
        }
        break;

      default:
    }
  };

  /*--------------------------------------------------------------------------------*/
  /* To open Search Bar                                                              */
  /*--------------------------------------------------------------------------------*/
  toggleMenu = () => {
    document.getElementById("search").classList.toggle("show-search");
  };

  render() {
    const { data } = this.props;
    const { isOpen, loggoutModal } = this.state;
    return (
      <header
        className="topbar navbarbg"
        data-navbarbg={data.settings[0].navbarbg}
      >
        <Navbar className="top-navbar navbar-dark" expand="md">
          <div
            className="navbar-header"
            id="logobg"
            data-logobg={data.settings[0].logobg}
          >
            {/*--------------------------------------------------------------------------------*/}
            {/* Mobile View Toggler  [visible only after 768px screen]                         */}
            {/*--------------------------------------------------------------------------------*/}
            <a
              className="nav-toggler d-block d-md-none text-white"
              onClick={this.showMobilemenu}
            >
              <i className="ti-menu ti-close" style={{ fontSize: "1.5rem" }} />
            </a>
            {/*--------------------------------------------------------------------------------*/}
            {/* Logos Or Icon will be goes here for Light Layout && Dark Layout                */}
            {/*--------------------------------------------------------------------------------*/}
            <NavbarBrand href="/">
              <span className="logo-text">
                <img
                  src={logolighttext}
                  className="light-logo"
                  alt="homepage"
                  style={{ height: 55 }}
                />
              </span>
            </NavbarBrand>
            {/*--------------------------------------------------------------------------------*/}
            {/* Mobile View Toggler  [visible only after 768px screen]                         */}
            {/*--------------------------------------------------------------------------------*/}
            <a
              className="topbartoggler d-block d-md-none text-white"
              onClick={this.toggle}
            />
          </div>
          <Collapse
            className="navbarbg"
            isOpen={isOpen}
            navbar
            data-navbarbg={data.settings[0].navbarbg}
          >
            <Nav className="float-left d-none" navbar>
              <NavItem>
                <NavLink
                  href="#"
                  className="d-none d-md-block"
                  onClick={this.sidebarHandler}
                >
                  <i className="ti-menu" />
                </NavLink>
              </NavItem>
            </Nav>
            <Nav className="ml-auto float-right" navbar>
              <div className="text-white mr-3 my-auto">
                Seja bem vindo, <b>Fernando!</b>
              </div>
              <Button
                onClick={this.toggleLoggoutModal}
                className="btn btn-outline-light mr-3 "
              >
                Sair
              </Button>
              <div
                className="navbar-header"
                id="logobg"
                data-logobg={data.settings[0].logobg}
              />
            </Nav>
          </Collapse>
        </Navbar>
        <Modal isOpen={loggoutModal} toggle={this.toggleLoggoutModal}>
          <ModalHeader toggle={this.toggleLoggoutModal}>
            <i className="fa fa-exclamation-circle" /> Ateno
          </ModalHeader>
          <ModalBody>Deseja realmente sair?</ModalBody>
          <ModalFooter>
            <Button color="primary" onClick={this.loggout}>
              Sair
            </Button>
            <Button color="secondary" onClick={this.toggleLoggoutModal}>
              Cancelar
            </Button>
          </ModalFooter>
        </Modal>
      </header>
    );
  }
}
export default Header;



