const validator = {
  password: {
    rules: [
      {
        test: x => x && x.length > 4,
        message: "A senha precisa ser maior do que 4 caractres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  passwordConfirmation: {
    rules: [
      {
        test: (x, y) => x === y,
        message: "Confirme a senha."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  passwordOld: {
    rules: [
      {
        test: x => x && x.length > 4,
        message: "A senha precisa ser maior do que 4 caractres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  }
};

export default validator;



