import React, { Component } from "react";
import {
  Row,
  Col,
  Card,
  CardBody,
  CardTitle,
  Button,
  Form,
  Label,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Collapse
} from "reactstrap";
import Validator from "./validator";
import InputBanky from "../../../../components/input-banky/input-banky";
import { toCurrency } from "../../../../util/currency-formatter";

export class ScheduleBoleto extends Component {
  state = {
    statusScheduleBoleto: false,
    modalToggler: false,
    editable: false,
    form: {
      value: "",
      date: ""
    },
    value: "",
    date: ""
  };

  constructor(props) {
    super(props);
    this.validators = Validator;
  }

  toCurrency = numberString => {
    const number = parseFloat(numberString.replace(/[^0-9,]/g, ""));
    return new Intl.NumberFormat("pt-BR", {
      style: "currency",
      minimumFractionDigits: 2,
      maximumFractionDigits: 2,
      currency: "BRL"
    }).format(number);
  };

  handleComponentChanges = input => e => {
    const { form: formFields } = this.state;
    formFields[input] =
      input === "value" ? toCurrency(e.target.value) : e.target.value;
    this.checkFormErros(input);
    this.setState({ form: formFields });
  };

  updateScheduleValues = () => {
    const {
      form: { date, value }
    } = this.state;
    const status = this.validForm();
    if (status) {
      this.setState({ statusScheduleBoleto: true, date, value });
      this.toggleEditable();
    }
  };

  checkFormErros = input => {
    const {
      form: { [input]: inputValue }
    } = this.state;
    if (this.validators[input].errors.length !== 0)
      this.validators[input].rules.forEach(rule => {
        if (rule.test instanceof RegExp) {
          this.validators[input].errors = [];
          if (!rule.test.test(inputValue)) {
            this.validators[input].errors.push(rule.message);
            this.validators[input].valid = false;
          }
        } else if (typeof rule.test === "function") {
          if (!rule.test(inputValue)) {
            this.validators[input].errors.push(rule.message);
            this.validators[input].valid = false;
          }
        }
      });
  };

  validForm = () => {
    let status = true;
    const { form: formFields } = this.state;
    Object.keys(formFields).forEach(field => {
      const { [field]: currentState } = formFields;
      this.formValidators(field, currentState);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  };

  deactiveSchedule = () => {
    this.setState({
      form: { value: "", date: "" },
      statusScheduleBoleto: false,
      editable: false,
      modalToggler: false
    });
  };

  toggle = () => {
    const { statusScheduleBoleto } = this.state;
    this.setState({
      statusScheduleBoleto: !statusScheduleBoleto,
      editable: true
    });
  };

  modalToggler = () => {
    const { modalToggler } = this.state;
    this.setState({ modalToggler: !modalToggler });
  };

  toggleEditable = () => {
    const { editable } = this.state;
    this.setState({ editable: !editable, form: { value: "", date: "" } });
  };

  renderForm = () => {
    const {
      form: { value, date }
    } = this.state;

    return (
      <Form className="form-material scheduleRechargeComponent-form">
        <Row>
          <Col xs="12">
            <Row className="justify-content-start">
              <Col xs="6">
                <Label>
                  Qual valor que voc deseja carregar no carto Banky todo ms?
                </Label>
              </Col>
              <Col xs="6">
                <Label>Qual dia do ms que deseja receber o boleto?</Label>
              </Col>
              <Col xs="6">
                <InputBanky
                  label="Valor"
                  type="text"
                  id="inputId"
                  autoComplete="off"
                  onChange={this.handleComponentChanges("value")}
                  value={value}
                  name="value"
                  mask={this.validators.value.mask}
                  formNoValidate
                  required
                />
                {this.showErrors("value")}
              </Col>
              <Col xs="6">
                <InputBanky
                  label="Dia do ms"
                  type="text"
                  onChange={this.handleComponentChanges("date")}
                  value={date}
                  autoComplete="off"
                  name="date"
                  mask={this.validators.date.mask}
                  formNoValidate
                  required
                />
                {this.showErrors("date")}
              </Col>
            </Row>
          </Col>
        </Row>
      </Form>
    );
  };

  renderInfo = () => {
    const { value, date } = this.state;
    return (
      <Row className="text-center mb-3 info">
        <Col xs="12">
          <Row>
            <Col xs="6">
              <Label>Valor mensal</Label>
            </Col>
            <Col xs="6">
              <Label>Dia do ms</Label>
            </Col>
            <Col xs="6">{value}</Col>
            <Col xs="6">{date}</Col>
          </Row>
        </Col>
      </Row>
    );
  };

  renderSaveCancelButtons = () => (
    <Row className="bBar">
      <Col>
        <Button
          type="button"
          color="danger"
          className="float-right"
          onClick={this.toggleEditable}
        >
          Cancelar
        </Button>
        <Button
          type="button"
          color="primary"
          className="mx-2 float-right"
          onClick={this.toggleEditable}
        >
          Salvar
        </Button>
      </Col>
    </Row>
  );

  renderButtons = () => {
    const { statusScheduleBoleto } = this.state;
    return (
      <Col xs="auto">
        <Button
          color={statusScheduleBoleto ? "danger" : "primary"}
          className="px-3"
          onClick={
            statusScheduleBoleto ? this.modalToggler : this.toggleEditable
          }
        >
          {statusScheduleBoleto ? "Desativar" : "Ativar"}
        </Button>
        <Button
          hidden={!statusScheduleBoleto}
          color="primary"
          className="ml-3"
          onClick={this.toggleEditable}
        >
          Editar
        </Button>
      </Col>
    );
  };

  formModal = () => {
    const { editable } = this.state;

    return (
      <Modal
        size="lg"
        isOpen={editable}
        toggle={this.toggleEditable}
        className="danger"
      >
        <ModalHeader toggle={this.toggleEditable}>
          Recarga Recorrente
        </ModalHeader>
        <ModalBody>{this.renderForm()}</ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={this.updateScheduleValues}>
            Atualizar
          </Button>
          <Button color="secondary" onClick={this.toggleEditable}>
            Cancelar
          </Button>
        </ModalFooter>
      </Modal>
    );
  };

  deactiveConfirmationModal = () => {
    const { modalToggler } = this.state;
    return (
      <Modal
        isOpen={modalToggler}
        toggle={this.modalToggler}
        className="danger"
      >
        <ModalHeader toggle={this.modalToggler}>
          <i className="fa fa-exclamation-circle" /> Ateno
        </ModalHeader>
        <ModalBody>
          Deseja realmente desativar os boletos automticos?
        </ModalBody>
        <ModalFooter>
          <Button color="primary" onClick={this.deactiveSchedule}>
            Desativar
          </Button>
          <Button color="secondary" onClick={this.modalToggler}>
            Cancelar
          </Button>
        </ModalFooter>
      </Modal>
    );
  };

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(st => st);
  }

  render() {
    const { statusScheduleBoleto } = this.state;
    return (
      <Card className="scheduleRechargeComponent">
        <CardBody>
          <Row className="justify-content-around">
            {/* Ttulo */}
            <Col xs="12">
              <CardTitle className="section-card-title">
                <i className="ti-back-right" /> Recarga Recorrente
              </CardTitle>
            </Col>
            <Col xs="12" className="my-3">
              <b>O que ?</b>
              <p className="text-justify">
                Deixe a recarga mensal do carto Banky programada! Assim voc
                no se preocupa em toda vez ter que pedir um novo boleto.
              </p>
            </Col>
            <Col xs="12" className="mb-3">
              <Collapse isOpen={statusScheduleBoleto}>
                {this.renderInfo()}
              </Collapse>
            </Col>
            {this.renderButtons()}
          </Row>
        </CardBody>
        {/* MODALS */}
        {this.deactiveConfirmationModal()}
        {this.formModal()}
      </Card>
    );
  }
}

export default ScheduleBoleto;



