import React from "react";
import { Alert, Row, Col, Card, CardBody, CardTitle, Button } from "reactstrap";
import { CardComponent } from "../../components/card-component/card-component";

class Block extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      errorVisibility: props.cardStatus === 0
    };

    this.onDismiss = this.onDismiss.bind(this);
  }

  onDismiss() {
    this.setState({ errorVisibility: false });
  }

  cardStatusToggler = () => {
    const { changeCardStatus, cardStatus } = this.props;
    if (cardStatus === 3) changeCardStatus(2);
    else changeCardStatus(3);
  };

  toBlock = () => {
    const { user, cardStatus } = this.props;
    const { errorVisibility } = this.state;
    return (
      <CardBody>
        <Alert color="warning" isOpen={errorVisibility} toggle={this.onDismiss}>
          O carto ainda no foi ativado
        </Alert>
        <Row>
          {/* Ttulo */}
          <Col xs="12">
            <CardTitle className="section-card-title">
              <i className="ti-lock" /> Bloquear carto
            </CardTitle>
          </Col>
          {/* Bloquear */}
          <Col xs="12" md="6" className="text-center  mb-3">
            <b>Deseja bloquear o carto?</b>
            <br />
            <Button
              className="btn btn-info mt-3"
              onClick={this.cardStatusToggler}
              disabled={cardStatus === 0}
            >
              Bloquear Carto
            </Button>
          </Col>

          {/* Imagem carto */}
          <Col xs="12" md="6" className="text-center mb-3">
            <CardComponent user={user} />
          </Col>
        </Row>
      </CardBody>
    );
  };

  toUnblock = () => {
    const { user } = this.props;
    return (
      <CardBody>
        <Row>
          {/* Ttulo */}
          <Col xs="12">
            <CardTitle className="section-card-title">
              <i className="ti-lock" /> Desbloquear carto
            </CardTitle>
          </Col>
          {/* Bloquear */}
          <Col xs="12" md="6" className="text-center  mb-3">
            <b>Voc achou o carto Banky?</b>

            <div className="text-muted my-3 text-center">
              Desbloquear o carto  to simples quanto bloquear. Basta clicar
              abaixo e pronto, o carto j pode ser usado novamente.
            </div>
            <Button className="btn btn-info" onClick={this.cardStatusToggler}>
              Desbloquear Carto
            </Button>
          </Col>

          {/* Imagem carto */}
          <Col xs="12" md="6" className="text-center mb-3">
            <CardComponent user={user} />
          </Col>
        </Row>
      </CardBody>
    );
  };

  renderCardStatus = () => {
    const { cardStatus } = this.props;
    return cardStatus === 3 ? this.toUnblock() : this.toBlock();
  };

  render() {
    return (
      <Row className="blockComponent">
        <Col className="widget">
          <Card>{this.renderCardStatus()}</Card>
        </Col>

        <Col className="widget">
          {/* Aviso */}
          <Card className="p-3">
            <CardBody>
              <CardTitle className="mb-0 text-left pb-1">
                Desbloquear carto
              </CardTitle>
              <div>
                <div className="text-muted  text-left my-3">
                  Caso voc recupere o carto perdido,  s mandar uma mensagem
                  para ns via whatsapp (11) 94507-1807 ou e-mail
                  resolvemos@banky.com.br que iremos te atender o mais rpido
                  possvel.
                </div>
              </div>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default Block;



