import React from "react";
import { Row, Col } from "reactstrap";
import { RechargeBoleto } from "./recharge-boleto/recharge-boleto";

export class RechargeNow extends React.Component {
  state = {
    user: {
      nCard: "5111 1111 1111 1111",
      valCard: "01/2020"
    }
  };

  render() {
    const { user } = this.state;
    return (
      <Row>
        <Col className="widget">
          <RechargeBoleto user={user} />
        </Col>
      </Row>
    );
  }
}

export default RechargeNow;



