import React, { Component } from "react";
import { Button, Row, Col, Form, Container } from "reactstrap";
import Axios from "axios";
import Validator from "./validator";
import InputBanky from "../../components/input-banky/input-banky";

export class StepTwo extends Component {
  constructor(props) {
    super(props);
    this.validators = Validator;
    this.state = props.values;
  }

  getCEP = () => {
    const { cepEmpresa } = this.state;
    if (Validator.cepEmpresa.rules[0].test.test(cepEmpresa)) {
      const config = {
        params: {
          app_key: "lNDckxoevbHQMeI9pnV08z5vYREJIKBr",
          app_secret: "x4QrgaMWBWA6j3aNKegfoFBp9qIhSWrbwpCmz9EG9HeUT7ry"
        }
      };

      Axios.get(`https://webmaniabr.com/api/1/cep/${cepEmpresa}`, config).then(
        res => {
          if (res.data.endereco) {
            const e = { target: { value: res.data.endereco } };
            this.handleComponentChanges("enderecoEmpresa")(e);
          }
          if (res.data.bairro) {
            const e = { target: { value: res.data.bairro } };
            this.handleComponentChanges("bairroEmpresa")(e);
          }
          if (res.data.cidade) {
            const e = { target: { value: res.data.cidade } };
            this.handleComponentChanges("cidadeEmpresa")(e);
          }
          if (res.data.uf) {
            const e = { target: { value: res.data.uf } };
            this.handleComponentChanges("estadoEmpresa")(e);
          }
        }
      );
    }
  };

  // Testa a validade das informaes e avana para prxima etapa
  continue = e => {
    const { values, nextStep } = this.props;
    e.preventDefault();
    this.setState(values);
    const res = this.validForm();
    if (res) nextStep();
  };

  handleComponentChanges = input => e => {
    const { handleChanges } = this.props;
    this.setState({ [input]: e.target.value });
    handleChanges(input, e.target.value);
  };

  validForm() {
    const { values } = this.props;
    let status = true;
    Object.keys(values).forEach(field => {
      this.formValidators(field, values[field]);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }

  formValidators(fieldName, value) {
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (!rule.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(prevState => prevState);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];

    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const {
      cepEmpresa,
      enderecoEmpresa,
      numeroEmpresa,
      complementoEmpresa,
      bairroEmpresa,
      cidadeEmpresa,
      estadoEmpresa
    } = this.state;
    return (
      <Container fluid className="steps h-100">
        {/*      */}
        {/* Form */}
        {/*      */}
        <Form className="form-material mb-auto">
          <Row className="justify-content-center align-content-top">
            <Col xs="12" className="mx-auto">
              <InputBanky
                label="CEP para entrega do carto"
                type="text"
                onChange={this.handleComponentChanges("cepEmpresa")}
                value={cepEmpresa}
                name="cepEmpresa"
                onBlur={this.getCEP}
                mask={this.validators.cepEmpresa.mask}
                formNoValidate
                required
              />
              {this.showErrors("cepEmpresa")}
            </Col>

            <Col xs="8" className="mx-auto">
              <InputBanky
                label="Endereo"
                onChange={this.handleComponentChanges("enderecoEmpresa")}
                type="text"
                value={enderecoEmpresa}
                name="enderecoEmpresa"
                mask={this.validators.enderecoEmpresa.mask}
                formNoValidate
                required
              />
              {this.showErrors("enderecoEmpresa")}
            </Col>

            <Col xs="4" className="mx-auto">
              <InputBanky
                label="Nmero"
                onChange={this.handleComponentChanges("numeroEmpresa")}
                type="text"
                value={numeroEmpresa}
                name="numeroEmpresa"
                formNoValidate
                mask={this.validators.numeroEmpresa.mask}
                required
              />
              {this.showErrors("numeroEmpresa")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Complemento (Opcional)"
                onChange={this.handleComponentChanges("complementoEmpresa")}
                type="text"
                value={complementoEmpresa}
                name="complementoEmpresa"
                formNoValidate
              />
              {this.showErrors("")}
            </Col>

            <Col xs="12" className="mx-auto">
              <InputBanky
                label="Bairro"
                onChange={this.handleComponentChanges("bairroEmpresa")}
                type="text"
                value={bairroEmpresa}
                name="bairroEmpresa"
                mask={this.validators.bairroEmpresa.mask}
                formNoValidate
                required
              />
              {this.showErrors("bairroEmpresa")}
            </Col>

            <Col xs="8" className="mx-auto">
              <InputBanky
                label="Cidade"
                onChange={this.handleComponentChanges("cidadeEmpresa")}
                type="text"
                value={cidadeEmpresa}
                mask={this.validators.cidadeEmpresa.mask}
                name="cidadeEmpresa"
                formNoValidate
                required
              />
              {this.showErrors("cidadeEmpresa")}
            </Col>

            <Col xs="4" className="mx-auto">
              <InputBanky
                label="Estado"
                onChange={this.handleComponentChanges("estadoEmpresa")}
                type="text"
                value={estadoEmpresa}
                mask={this.validators.estadoEmpresa.mask}
                name="estadoEmpresa"
                formNoValidate
                required
              />
              {this.showErrors("estadoEmpresa")}
            </Col>

            <Col xs="12" className="mx-auto">
              <Button
                size="lg"
                block
                type="submit"
                onClick={this.continue}
                className="btn btn-outline-light"
              >
                Continuar
              </Button>
            </Col>
          </Row>
        </Form>
      </Container>
    );
  }
}

export default StepTwo;



