import React, { Component } from "react";
import { Input, Label, FormGroup } from "reactstrap";
import InputMask from "react-input-mask";

export default class InputBanky extends Component {
  state = {
    isFocused: false,
    size: "xs"
  };

  constructor(props) {
    super(props);
    this.textInput = React.createRef();
    this.focusTextInput = this.focusTextInput.bind(this);
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
    this.state.value = props.value;
  }

  componentDidMount() {
    this.updateWindowDimensions();
    window.addEventListener("resize", this.updateWindowDimensions);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions);
  }

  handlerBlur = () => {
    const { onBlur } = this.props;
    if (onBlur) onBlur();
    this.setState({ isFocused: false });
  };

  handlerFocus = () => {
    // console.log(this.textInput);
    // this.textInput.current.selectionStart = 1;
    this.setState({ isFocused: true });
  };

  focusTextInput() {
    this.textInput.current.getInputDOMNode().focus();
  }

  updateWindowDimensions() {
    const w = {
      size: ""
    };
    if (window.innerWidth < 576) w.size = "xs";
    else if (window.innerWidth < 768) w.size = "sm";
    else if (window.innerWidth < 992) w.size = "md";
    else if (window.innerWidth < 1200) w.size = "lg";
    else w.size = "xl";
    this.setState(w);
  }

  render() {
    const { label, validation, mask, value, ...props } = this.props;
    const { isFocused, size } = this.state;

    //------------------------------------------------------------------
    // Comportamento padro
    //------------------------------------------------------------------
    let labelStyle = {
      position: "absolute",
      left: 0,
      bottom: !value && !isFocused ? "0" : "24px",
      fontSize: !value && !isFocused ? "14px" : "16px",
      lineHeight: !value && !isFocused ? "25px" : "29px",
      opacity: !value && !isFocused ? "1" : "0.8",
      color: "#fff",
      margin: 0,
      fontFamily: "Karla",
      transition: "all 0.35s ease-in-out"
    };
    let inputStyle = {
      fontFamily: "Karla",
      color: "#fff",
      fontSize: "16px",
      lineHeight: "20px",
      fontWeight: "bold"
    };
    let formGroupStyle = {
      paddingTop: "26px",
      marginBottom: 0
    };

    //------------------------------------------------------------------
    // Comportamento responsivo
    //------------------------------------------------------------------
    if (size === "xs" || size === "sm" || size === "md") {
      labelStyle = {
        position: "absolute",
        left: 0,
        bottom: !value && !isFocused ? "2px" : "24px",
        fontSize: !value && !isFocused ? "14px" : "16px",
        lineHeight: !value && !isFocused ? "17px" : "19px",
        color: "#fff",
        margin: 0,
        fontFamily: "Karla",
        transition: "all 0.35s ease-in-out"
      };
      inputStyle = {
        fontFamily: "Karla",
        color: "#fff",
        fontSize: "16px",
        lineHeight: "19px",
        fontWeight: "bold"
      };
      formGroupStyle = {
        paddingTop: "6px",
        marginBottom: 0
      };
    }
    return (
      <FormGroup style={formGroupStyle}>
        <Label style={labelStyle} onClick={this.focusTextInput}>
          {label}
        </Label>
        <Input
          innerRef={this.textInput}
          style={inputStyle}
          onFocus={this.handlerFocus}
          onBlur={this.handlerBlur}
          maskChar={null}
          mask={mask}
          tag={InputMask}
          value={value}
          {...props}
        />
      </FormGroup>
    );
  }
}



