const validator = {
  cnpj: {
    rules: [
      {
        test: value => {
          const cnpj = value.replace(/[^\d]+/g, "");
          if (cnpj === "") return false;

          if (cnpj.length !== 14) return false;

          // Elimina CNPJs invalidos conhecidos
          if (
            cnpj === "00000000000000" ||
            cnpj === "11111111111111" ||
            cnpj === "22222222222222" ||
            cnpj === "33333333333333" ||
            cnpj === "44444444444444" ||
            cnpj === "55555555555555" ||
            cnpj === "66666666666666" ||
            cnpj === "77777777777777" ||
            cnpj === "88888888888888" ||
            cnpj === "99999999999999"
          )
            return false;

          // Valida DVs
          let tamanho = cnpj.length - 2;
          let numeros = cnpj.substring(0, tamanho);
          const digitos = cnpj.substring(tamanho);
          let soma = 0;
          let pos = tamanho - 7;
          for (let i = tamanho; i >= 1; i -= 1) {
            soma += numeros.charAt(tamanho - i) * pos--;
            if (pos < 2) pos = 9;
          }
          let resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
          if (resultado.toString() !== digitos.charAt(0)) return false;

          tamanho += 1;
          numeros = cnpj.substring(0, tamanho);
          soma = 0;
          pos = tamanho - 7;
          for (let i = tamanho; i >= 1; i -= 1) {
            soma += numeros.charAt(tamanho - i) * pos--;
            if (pos < 2) pos = 9;
          }
          resultado = soma % 11 < 2 ? 0 : 11 - (soma % 11);
          if (resultado.toString() !== digitos.charAt(1)) return false;

          return true;
        },
        message: "Informe um CNPJ vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99.999.999/9999-99"
  },
  cpfResponsavel: {
    rules: [
      {
        test: value => {
          let Soma;
          let Resto;
          Soma = 0;
          const strCPF = value.replace(/[^\d]+/g, "");
          if (strCPF === "00000000000") return false;

          for (let i = 1; i <= 9; i++)
            Soma += parseInt(strCPF.substring(i - 1, i)) * (11 - i);
          Resto = (Soma * 10) % 11;

          if (Resto == 10 || Resto == 11) Resto = 0;
          if (Resto != parseInt(strCPF.substring(9, 10))) return false;

          Soma = 0;
          for (let i = 1; i <= 10; i++)
            Soma += parseInt(strCPF.substring(i - 1, i)) * (12 - i);
          Resto = (Soma * 10) % 11;

          if (Resto == 10 || Resto == 11) Resto = 0;
          if (Resto != parseInt(strCPF.substring(10, 11))) return false;
          return true;
        },
        message: "Informe um CPF vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "999.999.999-99"
  },
  nomeFantasia: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe um nome com pelo menos trs caractres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  telefoneEmpresa: {
    rules: [
      {
        test: /^\([0-9]{2}\) [0-9]{4,5} - [0-9]{4}/,
        message: "Informe um telefone (com ddd)"
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: input =>
      input.replace(/\D/g, "", "").length <= 10
        ? "(99) 9999 - 9999?"
        : "(99) 99999 - 9999"
  },
  nomeResponsavel: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe um nome com pelo menos trs caractres."
      }
    ],
    errors: [],
    valid: false,
    state: ""
  },
  cepEmpresa: {
    rules: [
      {
        test: /[0-9]{5}-*[0-9]{3}/,
        message: "Informe um cep vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "99999-999"
  },
  complementoEmpresa: {
    rules: [],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  enderecoEmpresa: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe um endereo vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  numeroEmpresa: {
    rules: [
      {
        test: /[_a-z0-9]{1,6}/,
        message: "Invlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "******"
  },
  bairroEmpresa: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe um bairro vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  cidadeEmpresa: {
    rules: [
      {
        test: x => x && x.length > 3,
        message: "Informe uma cidade vlida."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  estadoEmpresa: {
    rules: [
      {
        test: x => x && x.length > 1,
        message: "UF invlida."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: "aa"
  },
  email: {
    rules: [
      {
        test: /^[_a-z0-9-]+(\.[_a-z0-9-]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,})$/i,
        message: "Informe um e-mail vlido."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  password: {
    rules: [
      {
        test: x => x && x.length > 6,
        message: "A senha precisa ser maior do que 6 caractres."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  },
  passwordConfirmation: {
    rules: [
      {
        test: (x, y) => x === y,
        message: "Confirme a senha."
      }
    ],
    errors: [],
    valid: false,
    state: "",
    mask: ""
  }
};

export default validator;



