import React, { Component } from "react";

export default class StepsBanky extends Component {
  constructor(props) {
    super(props);
    this.state = { steps: props.steps, maxSteps: props.maxSteps };
  }

  componentWillReceiveProps({ steps }) {
    this.setState({ ...this.state, steps });
  }

  goBack = number => () => {
    const { maxSteps } = this.state;
    const { prevStep } = this.props;
    if (number <= maxSteps) prevStep(number);
  };

  drawState = number => {
    const { steps } = this.state;
    if (number < steps) return this.drawPrevious(number);
    if (number === steps) return this.drawCurrent(number);
    if (number > steps) return this.drawNext(number);
    return this.drawCurrent(number);
  };

  drawPrevious = number => (
    <div className="component" key={number.toString()}>
      <span className="circle-wrapper" onClick={this.goBack(number)}>
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          className="circle-active"
          focusable="false"
          viewBox="0 0 10 10"
          aria-hidden="true"
          role="presentation"
        >
          <circle className="circle" cx="5" cy="5" r="3" />
          <text className="number-label" x="5" y="6.5" textAnchor="middle">
            {number}
          </text>
        </svg>
      </span>
    </div>
  );

  drawCurrent = number => (
    <div className="component" key={number.toString()}>
      <span className="circle-wrapper">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          className="circle-current"
          focusable="false"
          viewBox="0 0 10 10"
          aria-hidden="true"
          role="presentation"
        >
          <circle className="circle" cx="5" cy="5" r="3" />
          <text className="number-label" x="5" y="6.5" textAnchor="middle">
            {number}
          </text>
        </svg>
      </span>
    </div>
  );

  drawNext = number => (
    <div className="component" key={number.toString()}>
      <span className="circle-wrapper">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          version="1.1"
          className="circle-inactive"
          focusable="false"
          viewBox="0 0 10 10"
          aria-hidden="true"
          role="presentation"
        >
          <circle className="circle" cx="5" cy="5" r="3" />
          <text className="number-label" x="5" y="6.5" textAnchor="middle">
            {number}
          </text>
        </svg>
      </span>
    </div>
  );

  drawLine = number => (
    <div key={number + 0.5} className="line-wrapper">
      <span className="line" />
    </div>
  );

  drawElements() {
    const { maxSteps } = this.state;
    const line = [];

    for (let i = 1; i <= maxSteps; i += 1) {
      line.push(this.drawState(i));
      if (i < maxSteps) line.push(this.drawLine(i));
    }
    return line;
  }

  render() {
    return <div className="steps-banky">{this.drawElements()}</div>;
  }
}



