import React, { Component } from "react";
import { Button, Row, Col, Form, Card, CardBody, CardTitle } from "reactstrap";
import InputBanky from "../../components/input-banky/input-banky";
import { CardComponent } from "../../components/card-component/card-component";
import Validator from "./validator";

export class Change extends Component {
  state = {
    password: "",
    passwordConfirmation: "",
    passwordOld: ""
  };

  constructor(props) {
    super(props);
    this.validators = Validator;
  }

  handleComponentChanges = input => e => {
    this.setState({ [input]: e.target.value });
  };

  changePassword = () => {
    if (this.validForm()) console.log("foi");
  };

  validForm() {
    let status = true;
    Object.keys(this.state).forEach(field => {
      this.formValidators(field, this.state[field]);
      if (!this.validators[field].valid) {
        status = false;
      }
    });
    return status;
  }

  formValidators(fieldName, value) {
    console.log(this.validators);
    console.log(fieldName, value);
    console.log(this.state);
    this.validators[fieldName].errors = [];
    this.validators[fieldName].state = value;
    this.validators[fieldName].valid = true;
    this.validators[fieldName].rules.forEach(rule => {
      if (rule.test instanceof RegExp) {
        if (!rule.test.test(value)) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      } else if (typeof rule.test === "function") {
        if (
          fieldName === "passwordConfirmation" &&
          !rule.test(this.validators.password.state, value)
        ) {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
          this.setState(prevState => prevState);
        } else if (!rule.test(value) && fieldName !== "passwordConfirmation") {
          this.validators[fieldName].errors.push(rule.message);
          this.validators[fieldName].valid = false;
        }
      }
    });
    this.setState(prevState => prevState);
  }

  showErrors(fieldName) {
    if (fieldName === "") {
      return <div className="error" />;
    }
    const validator = this.validators[fieldName];
    if (validator && !validator.valid) {
      const errors = validator.errors.map((info, index) => (
        // eslint-disable-next-line react/no-array-index-key
        <span className="error" key={index}>
          <i className="mdi mdi-alert-octagram" /> {info}
        </span>
      ));
      return <div className="error">{errors}</div>;
    }
    return <div className="error" />;
  }

  render() {
    const { password, passwordConfirmation, passwordOld } = this.state;
    const { user } = this.props;
    return (
      <Row>
        <Col className="widget">
          <Card className="changeComponent">
            <CardBody>
              <Row className="justify-content-around">
                {/* Ttulo */}
                <Col xs="12">
                  <CardTitle className="section-card-title">
                    <i className="ti-key" /> Alterar senha do carto
                  </CardTitle>
                </Col>

                {/* Bloquear */}
                <Col xs="12" md="6" className="text-center">
                  <div className="mb-3 text-left">
                    Digite abaixo sua nova senha do carto
                  </div>
                  <Form className="form-material">
                    <Row>
                      <Col xs="12">
                        <InputBanky
                          label="Senha atual"
                          type="password"
                          onChange={this.handleComponentChanges("passwordOld")}
                          value={passwordOld}
                          name="passwordOld"
                          mask=""
                          formNoValidate
                          required
                        />
                        {this.showErrors("passwordOld")}
                      </Col>

                      <Col xs="12">
                        <InputBanky
                          label="Nova senha"
                          type="password"
                          onChange={this.handleComponentChanges("password")}
                          value={password}
                          name="password"
                          mask=""
                          formNoValidate
                          required
                        />
                        {this.showErrors("password")}
                      </Col>
                      <Col xs="12">
                        <InputBanky
                          label="Confirmar nova senha"
                          type="password"
                          onChange={this.handleComponentChanges(
                            "passwordConfirmation"
                          )}
                          value={passwordConfirmation}
                          name="passwordConfirmation"
                          mask=""
                          formNoValidate
                          required
                        />
                        {this.showErrors("passwordConfirmation")}
                      </Col>
                      <Col xs="auto" className="mx-auto mb-3">
                        <Button
                          block
                          type="button"
                          color="primary"
                          onClick={this.changePassword}
                          className="mt-3"
                        >
                          Continuar
                        </Button>
                      </Col>
                    </Row>
                  </Form>
                </Col>
                <Col xs="12" md="6" className="my-auto">
                  {/* Imagem carto */}
                  <CardComponent user={user} />
                </Col>
              </Row>
            </CardBody>
          </Card>
        </Col>
      </Row>
    );
  }
}

export default Change;



