import Fulllayout from "../layouts/fulllayout";
import Login from "../views/authentication/login";
import Queue from "../views/queue/queue";
import SelfRegister from "../views/self-register/self-register";

const indexRoutes = [
  { path: "/login", name: "Login", component: Login },
  { path: "/fila", name: "Fila", component: Queue },
  { path: "/cadastro", name: "Auto Cadastro", component: SelfRegister },
  { path: "/", name: "Home", component: Fulllayout }
];

export default indexRoutes;



